<template>
  <div class="login">
    <div class="top"></div>
    <div class="login-bg">
      <div class="logo"></div>
    </div>
    <div class="login-centen">
      <van-cell-group>
        <van-field
          label="用户名"
          placeholder="请输入用户名"
          left-icon="manager"
          type="text"
          v-model="userName.value"
          :error-message="userName.error"
          @input="veriUserPass(userName.value, 'validUsername', {key: 'userName', error: '4-8个(字母数字_)组合，必须字母开头'})"
        />

        <van-field
          label="密码"
          placeholder="请输入密码"
          left-icon="lock"
          :right-icon="password.icon"
          :type="password.type"
          @click-right-icon="switching('password')"
          :error-message="password.error"
          v-model="password.value"
          @input="veriUserPass(password.value, 'validPwd', {key: 'password', error: '6-16个(字母数字_)组合，必须含有大小写字母'})"
        />
      </van-cell-group>
      <!-- 登录按钮 -->
      <div class="determine">
        <van-button
          class="login-btn"
          type="info"
          text="确定"
          block
          :loading="isLoad"
          loading-text="登录中..."
          @click="login"
          :disabled="!isProhibit"
        ></van-button>
      </div>
      <div class="logined">
        <router-link class="login-text" to="/register">没有账号？注册</router-link>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
.login {
  .top {
    height: 24px;
  }
  .login-bg {
    height: 200px;
    background: url("../assets/images/timg.jpg") no-repeat center center;
    background-size: 100% 200px;
    position: relative;
    .logo {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background-color: #fff;
      box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.5);
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
  }

  .login-centen {
    margin-top: 40px;
  }
  .determine {
    margin: 30px 15px 0;
    .login-btn {
      border-radius: 21px;
    }
  }
  .logined {
    margin-top: 20px;
    text-align: center;
  }
  .login-text {
    color: #666;
  }
}
</style>
<script>
import { validForm } from "../tool/validForm";

import { createNamespacedHelpers } from "vuex";
//基于loginModule的命名空间导入mapState、mapMutations 辅助函数
const { mapState, mapMutations } = createNamespacedHelpers("loginModule");
export default {
  name: "login",

  //计算属性
  computed: {
    ...mapState(["password", "isLoad", "userName", "isProhibit"])
  },

  // 方法
  methods: {
    ...mapMutations(["switching"]),

    // 点击登录
    login() {
      this.$store.commit("loginModule/login", true);

      let randomTime = Math.random() * 5 * 1000;
      setTimeout(() => {
        //获取所有用户
        let users = localStorage.getItem("users");

        users = users ? JSON.parse(users) : [];
       
        if (users.length === 0) {
          this.$toast({
            duration: 2000,
            message: "该用户不存在"
          });
          
          this.$store.commit("loginModule/login", false);
          return;
        }

        //查找用户
        for (let i = 0; i < users.length; i++) {
          if (users[i].username == this.userName.value) {
            //如果该用户存在
            //匹配密码
            if (users[i].password == this.password.value) {
              this.$toast({
                duration: 2000,
                message: '登录成功'
              })
              this.$store.commit('loginModule/login', false);

              //跳转到首页
              //记录用户登录状态
              let user = {
                isLogin: true,
                username: this.userName.value
              };
              localStorage.setItem('user', JSON.stringify(user));

              return this.$router.push({name: 'home'});
            } else {
              this.$toast({
                duration: 2000,
                message: '用户名或者密码不正确'
              })

              this.$store.commit('loginModule/login', false);
              return;
            }
          }
        }

        //用户不存在
        this.$toast({
          duration: 2000,
          message: '该用户不存在'
        })
        this.$store.commit('loginModule/login', false);
        
      }, randomTime);
    },

    // 验证用户名和密码
    veriUserPass(value, fnName, o) {
      let isProhibit = validForm[fnName](value);
      o.isProhibit = isProhibit;
      this.$store.commit("loginModule/veriUserPass", o);

      this.$store.commit("loginModule/adopt");
    }
  }
};
</script>
 